import React from 'react';
import { connect } from 'react-redux';
import {Button} from 'antd-mobile';
import Tab from './Tab';

//登录按钮样式
const logbtnStyle={
    width:'100px',
    height:'50px',
    backgroundColor:'#ff4500',
    color:'#F0FFFF',
    fontSize:'20px',
    cursor:'pointer'
}
//取消收藏按钮样式
const delBtnStyle={
    width:'140px',
    height:'35px',
    backgroundColor:'red',
    lineHeight:'35px',
    color:'#F0FFFF',
    cursor:'pointer',
    margin:0
}

const Favorite = (props) => {
    const log = () => {
        props.history.push('/reactWork5/login');
    }
    const datalist = props.collection;
    
    let username = props.username || localStorage.getItem('username');
    return (
        <div>
            <div>
            {
                username
                ? <div style={{width:'100%',padding:0}}>
                    {/* 收藏列表 */}
                    <div>
                        <h2>收藏列表</h2>
                        <div>
                            <ul style={{listStyle:'none',padding:'5%'}}>
                                {
                                    datalist.map((item,index)=>{
                                        return (
                                            <li key={index} style={{display:'inline-block',marginBottom:'5px'}}>
                                                <img src={item.img}
                                                    style={{width:'30%',display:'inline-block'}}
                                                    alt='加载失败'
                                                ></img>&nbsp;&nbsp;&nbsp;
                                                <div style={{display:'inline-block'}}>
                                                    <span
                                                        style={{fontSize:'20px'}}
                                                    >{item.title}&nbsp;&nbsp;&nbsp;</span>
                                                    <span style={{color:'red'}}>￥{item.price}</span>
                                                    <br/><br/><br/>
                                                    <Button 
                                                        style={delBtnStyle} 
                                                        onClick={()=>{
                                                                props.dispatch({
                                                                    type:'DEL_COLLECTION',
                                                                    index:index
                                                                });
                                                            }
                                                        }>
                                                        <i className='iconfont icon-shanchu'></i>
                                                        &nbsp;取消收藏
                                                    </Button>
                                                </div>
                                            </li>
                                        )
                                    })
                                }
                            </ul>
                            <div style={{height:'50px'}}></div>
                        </div>
                    </div>
                </div>
                : <div style={{width:'100%',padding:'150px 80px'}}>
                    <h2>您还未登录，请先登录</h2>
                    <Button style={logbtnStyle}
                        onClick={log}
                    >去登录 →</Button>
                    <h3>
                        从登录页返回后，需要时间加载
                        <br/>
                        请稍后...
                    </h3>
                </div>
            }
            </div>
            <Tab />
        </div>
    )
}
const mapStateToProps = (state) => ({
    username: state.userinfo.username,
    collection:state.shoucang
})
export default connect(mapStateToProps)(Favorite);
