
const login = ()=>{
    return (dispatch)=>{
        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/login';
        fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
        })
        .then(res => res.json())
        .then(res => {
            console.log();
            dispatch({
                type:'LOGIN',
                username:res.data.name            
            });
            localStorage.setItem('username',res.data.name);
        });
    }
};
export default login;
