let collectionId = [];
function collect(state=collectionId,action){
    if(action.type === 'ADD_COLLECTION'){
        return [...state,action.value]
    }
    if(action.type === 'DEL_COLLECTION'){
        // action.index
        let arr = [...state];
        arr.splice(action.index,1);
        return arr;
    }
    return state;
};

export default collect;