import React from 'react';
import { BrowserRouter as Router,Route,Switch,Redirect} from 'react-router-dom';
import {Provider} from 'react-redux';
import Home from './taobao/Home';
import Favorite from './taobao/Favorite';
import Order from './taobao/Order';
import My from './taobao/My';
import Search from './taobao/Search';
import Details from './taobao/Details';
import CommodityGrid from './taobao/CommodityGrid';
import store from './tb_Redux/store';
import Login from './taobao/Login';

const Taobao = ()=>{
	return <Provider store={store}>
		<Router>
			<Switch>
				<Route exact path='/reactWork5/' component={Home} />
				<Route path='/reactWork5/favorite' component={Favorite} />
				<Route path='/reactWork5/order' component={Order} />
				<Route path='/reactWork5/my' component={My} />
				<Route path='/reactWork5/search' component={Search}/>
				<Route path='/reactWork5/details' component={Details}/>
				<Route path='/reactWork5/commoditygrid' component={CommodityGrid}/>
				<Route path='/reactWork5/login' component={Login}/>
				<Route render={()=><Redirect to='/reactWork5/' component={Home}/>} />
			</Switch>
		</Router>
	</Provider>
}
export default Taobao;