//商品列表
import React,{useEffect} from 'react';
import { Grid } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import getGoodslist from '../tb_Redux/getGoodlist'; 


//商品链接样式
const commodity = {
    padding: 0,
    margin: '5px',
    marginTop: 0,
    marginBottom: '-12px',
    overflow: 'hidden',
    borderRadius: '10px',
    backgroundColor: '#fff'
}
//商品图片样式
const imgStyle = {
    padding: 0,
    margin: 0,
    width: '100%'
}

const CommodityGrid = (props) => {
    //获取商品列表
    useEffect(() => {
        props.dispatch(getGoodslist());
    },[])
    const data = props.data;
    return (
        <div>
            <Grid
                data={data}
                columnNum={2}
                hasLine={false}
                square={false}
                renderItem={(i) => {
                        return (
                            <div style={commodity}
                                onClick={() => { 
                                    props.history.push({pathname:'/reactWork5/details',state:i}) ;                                                      
                                    // props.history.push(`/details/${i.id}`) ;
                                }}>
                                <img src={i.img} style={imgStyle} alt="加载失败"/>
                                <div style={{ marginTop: '8px' }}>
                                    <p style={{ color: '#000', fontSize: '16px', }}>
                                        {i.title}
                                    </p>
                                    <span style={{ color: '#f00', fontSize: '14px', }}>￥{i.price}</span>&nbsp;&nbsp;
                                    <span style={{ color: '#888', fontSize: '14px', }}>{i.id+'人已购买'}</span>
                                </div>
                            </div>
                        )
                }}
                    
                itemStyle={{
                    background: 'rgba(0,0,0,.05)',
                    padding: 0
                }}
            />
        </div>
    )
}

const mapStateToProps = (state) => ({
    data:state.datalist.data,
    collection:state.shoucang
})

export default withRouter(connect(mapStateToProps)(CommodityGrid));