import React from 'react';
import {TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Tab extends React.Component {
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="#ff8c00"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-faxian-copy'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shouyex'></i>                           
                        }
                        selected={ pathname === '/reactWork5/' }
                        onPress={ () => {
                            this.props.history.push('/reactWork5/');
                        } }
                        data-seed="logId"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-shoucangjia'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucangjia1'></i>                           
                        }
                        title="收藏夹"
                        key="cart"
                        selected={ pathname === '/reactWork5/favorite' }
                        onPress={ () => {
                            this.props.history.push('/reactWork5/favorite')
                        } }
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-dingdanliebiao'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-dingdanliebiao1'></i>
                        }
                        title="订单列表"
                        key="Friend"
                        selected={ pathname === '/reactWork5/order' }
                        onPress={ () => {
                            this.props.history.push('/reactWork5/order')
                        } }
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-wode2'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-wodedangxuan'></i>
                        }
                        title="我的淘宝"
                        key="Friend"
                        selected={ pathname === '/reactWork5/my' }
                        onPress={ () => {
                            this.props.history.push('/reactWork5/my')
                        } }
                    ></TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Tab);