import React from 'react'
import { Button} from 'antd-mobile';
import { connect } from 'react-redux';

 
//返回箭头样式
const arrowStyle = {
    display: 'inline-block',
    width: '30px',
    height: '30px',
    textAlign: 'center',
    lineHeight: '30px',
    backgroundColor: 'rgba(0,0,0,0.3)',
    fontSize: '30px',
    color: 'rgba(255,255,255,0.8)',
    borderRadius: '50%',
    position: 'absolute',
    top: '3%',
    left: '5%'
}
//底部按钮样式
const divStyle = {
    fontSize:'16px',
    display:'inline-block',
    marginTop:'10px',
    marginLeft:'15px',
    color:'#949494'
}

const Details = (props) => {

    const goodInfo = props.location.state;
    //收藏图片路径
    const imgsrc=()=>{
        for(var i=0;i<props.collection.length;i++){
            if(goodInfo.id === props.collection[i].id){
                return 'iconfont icon-star__easyico'
            }
        }
        return 'iconfont icon-shoucang1'
    }

    let username = props.username || localStorage.getItem('username');
    
    return (
        <div>
            <div>
                <img src={goodInfo.img} alt="加载失败" style={{ width: '100%' }}/>
                <span style={arrowStyle}
                    onClick={() => {
                        props.history.push('/reactWork5/');
                    }}
                >&lt;</span>
            </div>
            <div style={{
                position: 'fixed',
                width: '100%',
                bottom: 0,
                backgroundColor: '#fff'
            }}>
                <div style={{ width: '40%', display: 'inline-block' }}>
                    <div style={divStyle}>
                        &nbsp;<i className='iconfont icon-dianpu'></i>
                        <br/>
                        店铺
                    </div>
                    <div style={divStyle}>
                        &nbsp;<i className='iconfont icon-kefu'></i>
                        <br/>
                        客服
                    </div>
                    <div style={divStyle}>
                        &nbsp;<i 
                            className= {imgsrc()}
                            onClick={(e)=>{
                                if(username){
                                    //进行收藏
                                    props.dispatch({
                                        type:'ADD_COLLECTION',
                                        value:goodInfo
                                    });
                                    e.target.className='iconfont icon-star__easyico';
                                }else{
                                    props.history.push('/reactWork5/login');
                                }
                            }}></i>
                        <br/>
                        收藏
                    </div>
                </div>
                <div
                    style={{
                        width: '60%',
                        float: 'right',
                        backgroundColor: '#fff',
                        paddingTop: '5px',
                        borderTop: '1px solid #dcdcdc',
                    }}>
                    <Button
                        style={{
                            backgroundColor: '#ffa500',
                            borderRadius: 0,
                            borderTopLeftRadius: '25px',
                            borderBottomLeftRadius: '25px',
                            width: '50%',
                            display: 'inline-block',
                            color: '#fff5ee'
                        }}>加入购物车
                </Button>
                    <Button
                        style={{
                            backgroundColor: '#ff6c00',
                            borderRadius: 0,
                            borderTopRightRadius: '25px',
                            borderBottomRightRadius: '25px',
                            width: '50%',
                            display: 'inline-block',
                            color: '#fff5ee'
                        }}
                    >立即购买</Button>
                </div>
            </div>
        </div>
    )
}

const mapStateToProps = (state) => ({
    username: state.userinfo.username,
    collection:state.shoucang
})
export default connect(mapStateToProps)(Details);
