import React from 'react';
import {Button} from 'antd-mobile';
import login from '../tb_Redux/loginaction';
import {connect} from 'react-redux';

//登录页样式
const divStyle = {
    width:'100%',
    paddingTop:'150px'
}

//登录按钮样式
const logbtnStyle={
    width:'100px',
    height:'50px',
    backgroundColor:'#ff4500',
    color:'#F0FFFF',
    fontSize:'20px',
    cursor:'pointer',
    marginLeft:'80px'
}
//输入框样式
const inpStyle={
    width:'200px',
    height:'50px',
    marginBottom:'10px',
    marginLeft:'80px',
    border:'2px solid #ff4500',
    borderRadius:'5px'

}
const Login = (props) => {
    const log = () =>{
        //登陆后跳转到首页
        props.dispatch(login());
        console.log('登录中...');
        props.history.goBack();
        
    }
    return (
        <div style={divStyle}>
            <input type='text' placeholder='用户名' style={inpStyle}></input>
            <input type='password' placeholder='密码' style={inpStyle}></input>
            <Button onClick={log} style={logbtnStyle}>登录</Button>
        </div>
    )
}
const mapStateToProps = (state) => ({
    username: state.userinfo.username
})
export default connect(mapStateToProps)(Login);